.own-desing {
    &__wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__info {
        flex: 0 1 45%;
        margin-right: auto;
        margin-left: auto;
        padding-right: 15px;
        padding-left: 15px;
        max-width: 605px;
        text-align: center;

        p {
            margin-bottom: 25px;
            line-height: 130%;
        }
    }

    &__image {
        position: relative;
        flex: 0 1 55%;
        border-radius: var(--radius);
        padding-top: 35%;

        img {
            position: absolute;
            left: 0;
            top: 0;
            border-radius: var(--radius);
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }
}
